import {StyleSheet, Text, View, TouchableOpacity, FlatList} from 'react-native';
import React from 'react';
// import {Item} from '../item/Item';
import moment from 'moment';
import {useSelector} from 'react-redux';
import CheckBox from '@react-native-community/checkbox';

export const List = props => {
  const dataList = useSelector(state => state.dataList);

  const onPressItem = item => {
    props.navigation.navigate('Detail', {item: item, screen: 'Edit Item'});
  };

  const renderItem = ({item, index}) => (
    <Item
      testID="item"
      CheckBox={item?.check}
      title={item?.title}
      description={item?.description}
      date={moment(item?.date).format('LLLL')}
      onPressItem={() => onPressItem(item)}
    />
  );

  return (
    <FlatList
      data={dataList}
      renderItem={renderItem}
      keyExtractor={item => item.id}
    />
  );
};

const Item = props => (
  <View style={styles.itemContainer} testID={props.testID}>
    <View style={styles.checkBox}>
      <CheckBox
        disabled={false}
        value={props.CheckBox}
        onValueChange={props.onValueChange}
      />
    </View>

    <TouchableOpacity
      onPress={() => props.onPressItem(props.item)}
      style={{flex: 5, justifyContent: 'center'}}>
      <Text style={styles.textTitle}>{props.title}</Text>
      <Text style={styles.text1}>{props.description}</Text>
      <Text style={styles.text1}>{props.date}</Text>
    </TouchableOpacity>
  </View>
);

const styles = StyleSheet.create({
  text1: {
    fontSize: 12,
    fontWeight: '500',
    color: 'grey',
  },
  textTitle: {
    fontSize: 16,
    fontWeight: 'bold',
    color: 'black',
  },
  itemContainer: {
    height: 80,
    width: '100%',
    flexDirection: 'row',
    marginBottom: 5,
  },
  checkBox: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
});
